@include respond('desktop'){
	&__toggle {
		position: relative;
		display: flex;
		width: 45px;
		height: 20px;
		border-width: 1px;
		border-color: rgb(255, 255, 255);
		border-style: solid;
		border-radius: 2em;
		background-color: #030b2e;

		&-left,
		&-right {
			z-index: 0;
			position: absolute;
			top: 0;
			display: flex;
			align-items: center;
			width: 50%;
			height: 100%;

			.icon-font {
				color: #ffffff;
				font-size: 12px;
				@include transition(opacity);

				&.icon-moon {
					opacity: 0;
					font-size: 9px;
				}
			}
		}

		&-left {
			left: 0;
			justify-content: flex-end;
		}

		&-right {
			right: 0;
			justify-content: flex-start;
		}

		input {
			opacity: 0;
			visibility: hidden;
			position: absolute;
			top: 0;
			left: 0;
			width: 0;
			height: 0;

			&:checked {
				& + label {
					.icon-font {
						@include transition(opacity);

						&.icon-sun {
							opacity: 0;
						}
						&.icon-moon {
							opacity: 1;
						}
					}

					span {
						transform: translateX(-25px);
						@include transition(transform);
					}
				}
			}
		}

		label {
			z-index: 2;
			cursor: pointer;
			position: absolute;
			top: 0;
			left: 0;
			display: flex;
			width: 100%;
			height: 100%;
		}

		span {
			position: absolute;
			top: 2px;
			right: 2px;
			border-radius: 50%;
			background-color: rgb(215, 242, 74);
			width: 14px;
			height: 14px;
			backface-visibility: hidden;
			transform-origin: center;
			@include transition(transform);
		}
	}

}

